import React, {useState, useEffect} from 'react'
import GoogleMap from 'google-map-react'
import LocationMarker from "./LocationMarker";
import LocationInfoBox from "./LocationInfoBox";
import {useCharacters} from "../contexts/CharactersContext";

const Map = ({center, zoom}) => {
	
	const {character} = useCharacters()
	
	const [locationInfo, setLocationInfo] = useState(null)
	
	const [markers,setMarkers] = useState([])
	
	useEffect(()=>{
		
		const charsArr = []
		if(character.marsh){
			charsArr.push(
				<LocationMarker
					lat={41.5990389}
					lng={-87.3423191}
					iconType={'rose1'}
					onClick={()=>{}}
					onMouseOver={()=>{
						setLocationInfo({
							info:"Марш\nКлан Тореадор\nЧлен катерии",
							visible:'block'
						})
					}}
					onMouseOut={()=>{
						setLocationInfo({
							info:"Поместье князя Модеуса\nПрием прошел успешно\nСобрать фото города",
							visible:'none'
						})
					}}
				
				/>
			
			)
		}
		if(character.jared){
			charsArr.push(
				<LocationMarker
					lat={41.6010389}
					lng={-87.3403191}
					iconType={'wolf'}
					onClick={()=>{}}
					onMouseOver={()=>{
						setLocationInfo({
							info:"Джаред\nКлан Гангрел\nЧлен катерии",
							visible:'block'
						})
					}}
					onMouseOut={()=>{
						setLocationInfo({
							info:"Поместье князя Модеуса\nПрием прошел успешно\nСобрать фото города",
							visible:'none'
						})
					}}
				
				/>
			
			)
		}
		
		setMarkers([...charsArr])
		
	},[character])
	
	return(
		<div className={'map'}>
			<GoogleMap
				bootstrapURLKeys={{key:'AIzaSyBdDXrVbMRNQ67za2Ci2P_MYDX1OCy7LXE'}}
				defaultCenter={center}
				defaultZoom={zoom}
			>
				{markers}
				<LocationMarker
					lat={41.5992389}
					lng={-87.3323191}
					iconType={'castle'}
					onClick={()=>{}}
					onMouseOver={()=>{
						setLocationInfo({
							info:"Поместье князя Модеуса\nПрием прошел успешно\nСобрать фото города",
							visible:'block'
						})
					}}
					onMouseOut={()=>{
						setLocationInfo({
							info:"Поместье князя Модеуса\nПрием прошел успешно\nСобрать фото города",
							visible:'none'
						})
					}}
					
				/>
			</GoogleMap>
			{locationInfo && <LocationInfoBox info={locationInfo}/>}
		</div>
	)
}

Map.defaultProps = {
	center: {
		lat: 41.6000514,
		lng: -87.337515,
	},
	zoom: 15
}

export default Map
