import React, {useEffect, useState} from 'react'
import {Icon} from '@iconify/react'
import castle from '@iconify-icons/emojione-monotone/castle'
import rose1 from '@iconify-icons/emojione-monotone/rose'
import wolf from '@iconify-icons/fa-brands/wolf-pack-battalion'

const LocationMarker = ({lat, lng, iconType,
	                        onClick,
	                        onMouseOver,
	                        onMouseOut
}) => {
	
	const [icon, setIcon] = useState(castle)
	useEffect(()=>{
		switch (iconType){
			case 'castle' : setIcon(castle); break;
			case 'rose1' : setIcon(rose1); break;
			case 'wolf' : setIcon(wolf); break;
		}
	}, [iconType])
	
	return(
		<div className={'location-marker'} onClick={onClick} onMouseOver={onMouseOver} onMouseOut={onMouseOut}>
			<Icon icon = {icon} className = "location-icon"/>
		</div>
	)
}

export default LocationMarker
