import React, {useEffect, useState} from 'react'
import Checkbox from '@material-ui/core/Checkbox'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import {Icon} from '@iconify/react'
import wolf from '@iconify-icons/fa-brands/wolf-pack-battalion'
import {useCharacters} from "../../contexts/CharactersContext";

const JaredCheck = () => {
	
	const {character, toggle} = useCharacters()
	
	const [checked, setChecked] = useState(false)
	
	
	useEffect(()=>{
		setChecked(character.jared)
	},[])
	
	
	return (
		<div>
			<FormControlLabel
				control={
					<Checkbox
						checked={checked}
						icon={<Icon icon={wolf}/>}
						checkedIcon={<Icon icon={wolf}/>}
						onChange={(e) => {
							setChecked(e.target.checked)
							toggle('jared', checked)
						}}
					/>}
				label={'Джаред'}/>
		</div>
	)
}

export default JaredCheck