import React, {useState,useEffect} from 'react'
import Checkbox from '@material-ui/core/Checkbox'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import {Icon} from '@iconify/react'
import rose1 from '@iconify-icons/emojione-monotone/rose'
import {useCharacters} from "../../contexts/CharactersContext";

const MarshCheck = () => {
	const {character, toggle} = useCharacters()
	const [checked, setChecked] = useState(false)
	
	useEffect(()=>{
		setChecked(character.marsh)
	},[])
	
	return (
		<div>
			<FormControlLabel
				control={
					<Checkbox
						checked={checked}
						icon={<Icon icon={rose1}/>}
						checkedIcon={<Icon icon={rose1}/>}
						onChange={(e) => {
							setChecked(e.target.checked)
							toggle('marsh', checked)
						}}
					/>}
				label={'Марш'}/>
		</div>
	)
}

export default MarshCheck